import React, { useState } from 'react';
import InputMask from 'react-input-mask';

import { FormControl, FormHelperText, InputBase, InputLabel, InputBaseProps } from '@mui/material';
import { useTheme } from '@mui/material/styles';

import { DeleteIcon, ShowPassIcon, HidePassIcon } from 'src/shared/icons';
import { colors } from 'src/theme';

import { beforeMaskedStateChange } from './utils';

type MyInputProps = InputBaseProps & {
    id: string;
    label?: string;
    value?: string;
    type: string;
    mask?: string;
    helperText?: string | boolean;
    isClearable?: boolean;
    onClear?: VoidFunction;
    defaultMaskedValue?: string;
};

export const CustomInput = ({
    id,
    label,
    value,
    type,
    error,
    helperText,
    isClearable,
    onClear,
    mask,
    onChange,
    defaultMaskedValue = '',
    ...rest
}: MyInputProps) => {
    const [isCapslockOn, setIsCapslockOn] = useState<boolean>(false);
    const [isShowPassword, setShowPassword] = useState(false);
    const theme = useTheme();
    const handleKeyPress = (e: React.KeyboardEvent<HTMLInputElement>) => {
        setIsCapslockOn(e.getModifierState('CapsLock'));
    };
    const handleTogglePasswordClick = () => {
        setShowPassword(!isShowPassword);
    };

    const passwordShowButton = isShowPassword ? (
        <HidePassIcon data-testid={`${id}-hideIcon`} onClick={handleTogglePasswordClick} sx={{ cursor: 'pointer' }} />
    ) : (
        <ShowPassIcon data-testid={`${id}-showIcon`} onClick={handleTogglePasswordClick} sx={{ cursor: 'pointer' }} />
    );

    const endAdornmentAttributes =
        value && isClearable && value !== defaultMaskedValue ? (
            <DeleteIcon data-testid={`${id}-deleteIcon`} onMouseDown={onClear} sx={{ cursor: 'pointer' }} />
        ) : !isClearable && type === 'password' ? (
            passwordShowButton
        ) : (
            rest.endAdornment
        );

    const style = {
        'label + &': {
            marginTop: 3,
        },
        '&.MuiInputBase-root': {
            [theme.breakpoints.down('tablet')]: {
                fontSize: '14px',
                lineHeight: '24px',
            },
            height: '44px',
            borderRadius: '4px',
            border: `1px solid ${colors.neutral400}`,
            fontSize: 16,
            padding: '10px 16px',
            fontFamily: 'SF Pro Display, sans-serif',
            '&.Mui-error': {
                border: `1px solid ${colors.alert}`,
            },
            '&.Mui-focused': {
                border: `1px solid ${colors.neutral600}`,
                '&.Mui-error': {
                    border: `1px solid ${colors.alert}`,
                },
            },
        },
    };

    const combinedHelperText = helperText || (isCapslockOn && ' CapsLock');
    return mask ? (
        <InputMask
            mask={mask}
            maskPlaceholder="_"
            alwaysShowMask={true}
            onChange={onChange}
            type={type}
            beforeMaskedStateChange={type === 'tel' ? beforeMaskedStateChange : undefined}
        >
            <FormControl variant="standard">
                <InputLabel shrink={true} htmlFor={id}>
                    {label}
                </InputLabel>
                <InputBase
                    id={id}
                    aria-describedby={`${id}-helper-text`}
                    error={error}
                    value={value || defaultMaskedValue}
                    onKeyDown={handleKeyPress}
                    {...rest}
                    endAdornment={endAdornmentAttributes}
                    inputProps={{ ...rest.inputProps }}
                    sx={style}
                />
                <FormHelperText error={error} id={`${id}-helper-text`} data-testid={`${id}-helpText`}>
                    {helperText}
                </FormHelperText>
            </FormControl>
        </InputMask>
    ) : (
        <FormControl fullWidth={rest.fullWidth} variant="standard">
            <InputLabel shrink={true} htmlFor={id}>
                {label}
            </InputLabel>
            <InputBase
                id={id}
                aria-describedby={`${id}-helper-text`}
                value={value}
                type={type === 'password' && isShowPassword ? 'text' : type}
                onChange={onChange}
                error={error || isCapslockOn}
                onKeyDown={handleKeyPress}
                {...rest}
                endAdornment={endAdornmentAttributes}
                inputProps={{ ...rest.inputProps }}
                sx={style}
                data-testid={`${id}-inputWrap`}
            />
            <FormHelperText error={error || isCapslockOn} id={`${id}-helper-text`} data-testid={`${id}-helpText`}>
                {combinedHelperText}
            </FormHelperText>
        </FormControl>
    );
};
