import { BeforeMaskedStateChangeStates } from 'react-input-mask';

const getIsStartNumber = (startSymbol: string): boolean => {
    switch (startSymbol) {
        case '8':
            return true;
        case '9':
            return true;
        default:
            return false;
    }
};

export function beforeMaskedStateChange({ nextState, currentState }: BeforeMaskedStateChangeStates) {
    let { value } = nextState;
    let selection = nextState.selection;
    const cursorPosition = selection ? selection.start : null;
    const lengthWithPlusSeven = 30;
    const lengthWithEightOrNine = 29;

    const startValueWithPlusSeven = currentState.value.slice(4, 6) === '+7';

    const startSymbol = currentState.value.slice(4, 5);

    const isStartNumber = getIsStartNumber(startSymbol);

    if (currentState.value.length === lengthWithPlusSeven && startValueWithPlusSeven) {
        value = currentState.value.slice(4, 16);
    }
    if (currentState.value.length === lengthWithEightOrNine && isStartNumber) {
        value = '+7' + currentState.value.slice(5, 15);
    }
    if (cursorPosition && !value.includes('_')) {
        selection = { start: value.length, end: value.length };
    }

    return {
        value,
        selection,
    };
}
